const SerCaballeros = 'laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus nam aut rem corrupti necessitatibusLorem ipsum dolor sit amet consectetursaepe quos, laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus facilis quam sunt vero nam aut rem corrupti necessitatibus.';
const SerDamas = 'laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus nam aut rem corrupti necessitatibusLorem ipsum dolor sit amet consectetursaepe quos, laudantium dolores iure, sint quibusdam ex hic officiis reprehenderit ducimus facilis quam sunt vero nam aut rem corrupti necessitatibus.';
