//Imagenes
let numeroIMG = 0;
let mostrarContenido = false;
function eventoDeTexto(){
    const imagenes = document.querySelectorAll('.galeria-servicios div');
    imagenes.forEach(imagen => {
        imagen.addEventListener('click', () => {
            numeroIMG = imagen.id;
            const body = document.querySelector('body');
            const click = document.querySelector(`.img-${numeroIMG}`);
            if (!mostrarContenido) {
                generarTextoCap();
                console.log(numeroIMG);
                mostrarContenido = true;
            } else {
                const borrar = document.getElementById(numeroIMG);
                while(borrar.firstChild){
                    borrar.removeChild(borrar.firstChild);
                    body.classList.remove('body-Color');
                    click.classList.remove('opacar');
                    mostrarContenido = false;
                }
            }
        });
    });
}

function generarTextoCap(){
    const body = document.querySelector('body');
    body.classList.add('body-Color');
    const mostrarDiv = document.querySelector(`.img-${numeroIMG}`);
    mostrarDiv.classList.add('opacar');
    mostrarDiv.classList.add('mostrar');
    const div = document.createElement('DIV');
    div.classList.add('div-size');
    const TituloCap = document.createElement('H5');
    const parrafo = document.createElement('P');
    if(numeroIMG === '1'){

        parrafo.textContent = SerCapilares;
        TituloCap.textContent = capilares;
    }
    if(numeroIMG === '2'){
        
        parrafo.textContent = SerCaballeros;
        TituloCap.textContent = caballeros;
    }
    if(numeroIMG === '3'){
        
        parrafo.textContent = SerDamas;
        TituloCap.textContent = damas;
    }
    if(numeroIMG === '4'){

        parrafo.textContent = SerPedicuraManicura;
        TituloCap.textContent = manicuraPedicura;
    }
    div.appendChild(TituloCap);
    div.appendChild(parrafo);
    mostrarDiv.appendChild(div);
}